// global variables
///////////////////////////////////////////////////////

//domain & urlDZIPrefix
let sDomain = getParams("domain");
if(sDomain != undefined)
	GlobalVars.domain = sDomain;
GlobalVars.urlDZIPrefix = sDomain + '/storage/';
console.log("url prefix: " + GlobalVars.urlDZIPrefix);

//original slideId
let s = getParams("slide");
if(s != undefined)
    GlobalVars.slideId = s;
console.log("slide: " + GlobalVars.slideId);

function getParams(name) {
	if (name = (new RegExp('[?&]' + encodeURIComponent(name) + '=([^&]*)')).exec(location.search))
		return decodeURIComponent(name[1]);
}

